import React, { Component } from 'react';
import { Link, hashHistory } from 'react-router';
import { connect } from 'react-redux';
import * as actions from '../../../actions';
import { RESET_ADMIN_DATA } from '../../../actions/types';
import { Spin,Table } from 'antd';
import moment from 'moment';

class Admin extends Component {
  constructor(props){
    super(props)
    this.state = {
      users: null,
    }
  }
  componentWillMount(){
    this.props.fetchAllUsers()
  }

  componentWillUnmount(){
    console.log('RESET_ADMIN_DATA')
    // this.props.dispatch({ type: RESET_ADMIN_DATA })
  }

  renderUsersTable(){
    if(this.props.users){
      const tableSetting = {
        bordered: true
      }
      const data = this.props.users.map(user => {
        return {
          key: user._id,
          ...user
        }
      })
      console.log('data', data);
      const columns = [{
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
        render: (name) => {
          return <span>{name.first + ' ' + name.last}</span>
        }
      }, {
        title: 'JoinAt',
        dataIndex: 'createAt',
        key: 'createAt',
        render: (createAt) => {
          return <span>{moment(createAt).format("L")}</span>
        }
      }, {
        title: 'yeahID',
        dataIndex: 'yeahID',
        key: 'address',
      }]
      return <Table {...tableSetting} columns={columns} dataSource={data} />
    }else{
      return <Spin size="large" />
    }
  }

  render() {
    return (
      <div className="display-component admin-component">
          <div className="header">Admin</div>
          <div className="content">
          <div className="container">
            <div>
              {this.renderUsersTable()}
            </div>
          </div>
          </div>
        </div>
    );
  }
};

function mapStateToProps({admin}){
    return { 
      users: admin.users
    }
}

export default connect(mapStateToProps, actions)(Admin);


